@import url(https://fonts.googleapis.com/css?family=Krub);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  background-color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-family: "Krub", sans-serif;
}

.App-header {
  color: white;
  text-align: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 46px;
  padding: 10px 0px 10px 40px;
  font-family: "Krub", sans-serif;
  background-color: #414141;
}

.App-body {
  text-align: center;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 40px;
  padding: 40px 40px 40px 40px;
}

.body-description {
  display: flex;
  flex-direction: column;
  border: 1px solid #414141;
  border-radius: 3px;
  height: 75vh;
}

.description-title {
  color: white;
  background-color: #414141;
  padding: 10px 10px 10px 10px;
}

.description-content {
  height: 100%;
  padding: 5px 20px 15px 20px;
  text-align: left;
}

.body-model {
  display: flex;
  flex-direction: column;
  border: 1px solid #414141;
  border-radius: 3px;
  height: 75vh;
}

.model-title {
  color: white;
  background-color: #414141;
  padding: 10px 10px 10px 10px;
}

.model-content {
  display: grid;
  grid-template-rows: 1fr 3fr;
  height: 100%;
  padding: 10px 20px 10px 20px;
}

.model-input {
  display: flex;
  flex-direction: column;
}

.input-title {
  display: grid;
  grid-template-columns: 3fr 3fr 1fr;
  margin: 5px 0px 10px 0px;
}

.input-title-blue {
  color: #7ec8fe;
  font-weight: bold;
  font-size: 22px;
}

.input-title-red {
  color: #e28092;
  font-weight: bold;
  font-size: 22px;
}

.input-teams {
  display: grid;
  grid-template-columns: 3fr 3fr 1fr;
  margin: 0px 0px 10px 0px;
  align-content: center;
}

.input-blue {
  text-align: center;
}

.blue-select {
  width: 200px;
  display: inline-block;
}

.input-red {
  text-align: center;
}

.red-select {
  width: 200px;
  display: inline-block;
}

.predict-button {
  height: 38px;
  width: 62px;
  background-color: #414141;
  font-size: 14px;
  color: white;
  border: 1px solid #414141;
  border-radius: 3px;
}

.predict-button:hover {
  cursor: pointer;
}

.predict-button:focus {
  outline: 0;
}

.input-results {
  display: grid;
  grid-template-columns: 3fr 3fr 1fr;
  margin: 0px 0px 10px 0px;
  align-content: center;
}

.model-history {
  /* overflow: scroll; */
  border-top: 1px solid #414141;
  margin: 10px 10px 10px 10px;
  padding: 15px 0px 10px 0px;
}

.bracket-pic {
  height: 210px;
  width: 100%;
}

.summary-table {
  width: 100%;
  font-size: 16px;
  color: #696969;
  border-collapse: separate;
}

.summary-loading {
  padding-top: 30px;
  text-align: center;
}

th {
  font-size: 18px;
}
tr.table-body-row > td {
  font-size: 16px;
  padding-top: 10px;
}

.blue-head {
  color: #7ec8fe;
}

.red-head {
  color: #e28092;
}

table tbody,
table thead {
  display: block;
}

table tbody {
  overflow: scroll;
  overflow-x: hidden;
  overflow-y: scroll;
  height: 235px;
}

th {
  width: 130px;
}

td {
  width: 130px;
}

.table-empty {
    padding: 30px;
    opacity: 0.8;
    height: 235px;
}

